.App {
  text-align: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #ff6b35 0%, #f7931e 25%, #ffd23f 50%, #06ffa5 75%, #4ecdc4 100%);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Arial', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.App::before {
  content: 'ðŸ‘¾';
  position: absolute;
  top: 10%;
  left: 10%;
  width: auto;
  height: auto;
  font-size: 6rem;
  opacity: 0.15;
  animation: slowHover 60s ease-in-out infinite;
  pointer-events: none;
  z-index: 0;
  will-change: transform;
}

.App::after {
  content: 'ðŸŽƒ';
  position: absolute;
  top: 20%;
  right: 15%;
  font-size: 4rem;
  opacity: 0.12;
  animation: floatPumpkin 45s ease-in-out infinite;
  pointer-events: none;
  z-index: 0;
  will-change: transform;
}

/* Additional floating monsters */
.App:before:nth-of-type(1) { animation-delay: -10s; }
.App:after:nth-of-type(1) { animation-delay: -15s; }

/* Ghost floating in top right */
.floating-ghost {
  content: 'ðŸ‘»';
  position: absolute;
  top: 5%;
  right: 25%;
  font-size: 3.5rem;
  opacity: 0.1;
  animation: floatGhost 35s ease-in-out infinite;
  pointer-events: none;
  z-index: 0;
  will-change: transform;
}

/* Skull floating in bottom left */
.floating-skull {
  content: 'ðŸ’€';
  position: absolute;
  bottom: 15%;
  left: 8%;
  font-size: 3rem;
  opacity: 0.13;
  animation: floatSkull 50s ease-in-out infinite reverse;
  pointer-events: none;
  z-index: 0;
  will-change: transform;
}

/* Robot monster floating in middle right */
.floating-robot {
  content: 'ðŸ¤–';
  position: absolute;
  top: 45%;
  right: 5%;
  font-size: 4.5rem;
  opacity: 0.11;
  animation: floatRobot 40s ease-in-out infinite;
  animation-delay: -20s;
  pointer-events: none;
  z-index: 0;
  will-change: transform;
}

/* Demon floating in bottom right */
.floating-demon {
  content: 'ðŸ‘¹';
  position: absolute;
  bottom: 25%;
  right: 30%;
  font-size: 3.8rem;
  opacity: 0.12;
  animation: floatDemon 42s ease-in-out infinite;
  animation-delay: -25s;
  pointer-events: none;
  z-index: 0;
  will-change: transform;
}

@keyframes slowHover {
  0% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  25% {
    transform: translate3d(60vw, 10vh, 0) rotate(5deg);
  }
  50% {
    transform: translate3d(70vw, 70vh, 0) rotate(-3deg);
  }
  75% {
    transform: translate3d(10vw, 60vh, 0) rotate(8deg);
  }
  100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
}

/* Individual animation keyframes for each monster */
@keyframes floatPumpkin {
  0%, 100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  25% {
    transform: translate3d(-30vw, 15vh, 0) rotate(-8deg);
  }
  50% {
    transform: translate3d(-50vw, -20vh, 0) rotate(5deg);
  }
  75% {
    transform: translate3d(-20vw, -35vh, 0) rotate(-12deg);
  }
}

@keyframes floatGhost {
  0%, 100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  30% {
    transform: translate3d(-40vw, 25vh, 0) rotate(6deg);
  }
  60% {
    transform: translate3d(-25vw, 50vh, 0) rotate(-4deg);
  }
  90% {
    transform: translate3d(-10vw, 30vh, 0) rotate(10deg);
  }
}

@keyframes floatSkull {
  0%, 100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  20% {
    transform: translate3d(35vw, -10vh, 0) rotate(-5deg);
  }
  50% {
    transform: translate3d(60vw, -25vh, 0) rotate(8deg);
  }
  80% {
    transform: translate3d(40vw, -40vh, 0) rotate(-3deg);
  }
}

@keyframes floatRobot {
  0%, 100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  33% {
    transform: translate3d(-25vw, -15vh, 0) rotate(4deg);
  }
  66% {
    transform: translate3d(-45vw, 20vh, 0) rotate(-7deg);
  }
}

@keyframes floatDemon {
  0%, 100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  40% {
    transform: translate3d(-30vw, -30vh, 0) rotate(9deg);
  }
  80% {
    transform: translate3d(-15vw, -10vh, 0) rotate(-6deg);
  }
}

/* Respect user's motion preferences and performance */
@media (prefers-reduced-motion: reduce) {
  .App::before,
  .App::after,
  .menu-container,
  .game-container,
  .results-container,
  .mode-button::before {
    animation: none;
  }
}

/* Disable expensive animations on lower-end devices */
@media (max-width: 768px), (max-height: 600px) {
  .App::after {
    display: none;
  }
  .menu-container, .game-container, .results-container {
    animation: none;
  }
  .mode-button::before {
    animation: none;
  }
}

/* Pause animations when user is actively typing */
body:has(input:focus) .App::before,
body:has(input:focus) .App::after,
body:has(input:focus) .floating-ghost,
body:has(input:focus) .floating-skull,
body:has(input:focus) .floating-robot,
body:has(input:focus) .floating-demon,
body:has(input:focus) .menu-container,
body:has(input:focus) .game-container,
body:has(input:focus) .results-container,
body:has(input:focus) .mode-button::before {
  animation-play-state: paused;
}

/* Performance optimization for input interactions */
.answer-input, .name-input {
  contain: layout style;
}

.answer-input:focus, .name-input:focus {
  contain: layout style paint;
}

/* Aggressive performance mode - disable all animations during any user interaction */
.App:hover .App::before,
.App:hover .App::after,
.App:hover .floating-ghost,
.App:hover .floating-skull,
.App:hover .floating-robot,
.App:hover .floating-demon,
.App:hover .menu-container,
.App:hover .game-container,
.App:hover .results-container {
  animation-play-state: paused;
}

/* Optimize button transitions for immediate response */
.mode-button {
  transition: transform 0.1s ease, filter 0.1s ease;
}

.submit-button, .done-button, .back-button {
  transition: transform 0.1s ease, background-color 0.1s ease;
}

.monster-container {
  background: linear-gradient(45deg, #ff6b35, #f7931e, #ffd23f);
  border: 4px solid #ff4757;
  border-radius: 25px;
  box-shadow: 0 25px 50px rgba(255, 71, 87, 0.3);
  position: relative;
  overflow: hidden;
}

.monster-container::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(45deg, #ff6b35, #06ffa5, #4ecdc4, #ffd23f);
  border-radius: 25px;
  z-index: -1;
  animation: rainbowBorder 3s ease-in-out infinite alternate;
}

@keyframes rainbowBorder {
  0% { filter: hue-rotate(0deg); }
  100% { filter: hue-rotate(360deg); }
}

.menu-container, .game-container, .results-container {
  background: linear-gradient(45deg, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.85));
  -webkit-backdrop-filter: blur(15px);
  backdrop-filter: blur(15px);
  border-radius: 30px;
  padding: 40px;
  border: 3px solid rgba(255, 107, 53, 0.3);
  box-shadow: 
    0 25px 50px rgba(255, 71, 87, 0.2),
    0 0 0 1px rgba(255, 255, 255, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.6);
  max-width: 500px;
  width: 90%;
  margin: 0 20px;
  position: relative;
  z-index: 1;
  animation: none;
  transform: translateZ(0);
  will-change: box-shadow;
}

@keyframes containerGlow {
  0% { 
    box-shadow: 
      0 25px 50px rgba(255, 71, 87, 0.15),
      0 0 0 1px rgba(255, 255, 255, 0.3),
      inset 0 1px 0 rgba(255, 255, 255, 0.6);
  }
  100% { 
    box-shadow: 
      0 28px 55px rgba(6, 255, 165, 0.2),
      0 0 0 1px rgba(255, 255, 255, 0.3),
      inset 0 1px 0 rgba(255, 255, 255, 0.6);
  }
}

/* Fallback for browsers that don't support backdrop-filter */
@supports not (backdrop-filter: blur(10px)) {
  .menu-container, .game-container, .results-container {
    background: rgba(255, 255, 255, 0.98);
  }
}

@media (min-width: 768px) {
  .menu-container, .game-container, .results-container {
    margin: 0 auto;
  }
}

.menu-container h1, .results-container h1 {
  background: linear-gradient(45deg, #ff6b35, #f7931e, #ffd23f, #06ffa5);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 2.8rem;
  margin-bottom: 10px;
  animation: gradientShift 3s ease infinite;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  font-weight: bold;
}

@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.menu-container p {
  color: #666;
  font-size: 1.2rem;
  margin-bottom: 30px;
}

.menu-buttons {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.mode-button {
  padding: 25px 35px;
  font-size: 1.4rem;
  background: #2c3e50;
  color: #ffffff;
  border: 4px solid #ff6b35;
  border-radius: 20px;
  cursor: pointer;
  transition: transform 0.1s ease, background-color 0.1s ease, border-color 0.1s ease;
  font-family: inherit;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
  font-weight: bold;
  position: relative;
  overflow: hidden;
  will-change: transform;
  box-shadow: 0 6px 15px rgba(44, 62, 80, 0.3);
}


@keyframes buttonGlow {
  0% { opacity: 0.8; transform: scale3d(1, 1, 1); }
  100% { opacity: 0.9; transform: scale3d(1.01, 1.01, 1); }
}

.mode-button.advanced {
  background: #8e44ad;
  border-color: #e74c3c;
  color: #ffffff;
}

.mode-description {
  font-size: 0.95rem;
  opacity: 1;
  font-weight: 500;
  color: #ecf0f1;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
}

.mode-button:hover {
  transform: translateY(-3px);
  background: #34495e;
  border-color: #f7931e;
  box-shadow: 0 10px 25px rgba(44, 62, 80, 0.4);
}

.mode-button.advanced:hover {
  background: #9b59b6;
  border-color: #ff6b35;
}


.game-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  font-size: 1.2rem;
  color: #667eea;
  font-weight: bold;
}

.timer {
  background: #ff6b6b;
  color: white;
  padding: 10px 15px;
  border-radius: 10px;
  font-size: 1.1rem;
}

.score {
  background: #4ecdc4;
  color: white;
  padding: 10px 15px;
  border-radius: 10px;
}

.question-container {
  margin-bottom: 30px;
}

.question {
  font-size: 3rem;
  color: #667eea;
  margin-bottom: 20px;
  font-weight: bold;
}

.answer-input {
  font-size: 1.4rem;
  padding: 15px 20px;
  border: 3px solid #667eea;
  border-radius: 15px;
  text-align: center;
  width: 200px;
  margin-bottom: 20px;
  margin-right: 10px;
  font-family: inherit;
  will-change: contents;
  transform: translateZ(0);
  display: inline-block;
  vertical-align: top;
}

.answer-input:focus {
  outline: none;
  border-color: #764ba2;
  box-shadow: 0 0 10px rgba(102, 126, 234, 0.3);
}

.submit-button {
  font-size: 1.3rem;
  padding: 15px 25px;
  background: #4ecdc4;
  color: white;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: transform 0.1s ease, background-color 0.1s ease;
  font-family: inherit;
  display: inline-block;
  vertical-align: top;
}

.submit-button:hover {
  background: #45b7b8;
  transform: translateY(-1px);
  transition: transform 0.1s ease, background-color 0.1s ease;
}

.game-controls {
  display: flex;
  gap: 15px;
  justify-content: center;
}

.done-button, .back-button {
  font-size: 1.1rem;
  padding: 12px 20px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.2s;
  font-family: inherit;
}

.done-button {
  background: #ff6b6b;
  color: white;
}

.done-button:hover {
  background: #ff5252;
  transform: translateY(-1px);
  transition: transform 0.1s ease, background-color 0.1s ease;
}

.back-button {
  background: #95a5a6;
  color: white;
}

.back-button:hover {
  background: #7f8c8d;
  transform: translateY(-1px);
  transition: transform 0.1s ease, background-color 0.1s ease;
}

.results {
  margin: 30px 0;
}

.result-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 15px 0;
  padding: 15px;
  background: #f8f9fa;
  border-radius: 10px;
  font-size: 1.2rem;
}

.result-label {
  color: #667eea;
  font-weight: bold;
}

.result-value {
  color: #4ecdc4;
  font-weight: bold;
  font-size: 1.3rem;
}

.feedback {
  margin: 20px 0;
  padding: 20px;
  border-radius: 15px;
  animation: feedbackSlide 0.3s ease-out;
}

.feedback.correct {
  background: linear-gradient(45deg, #2ecc71, #27ae60);
  color: white;
}

.feedback.incorrect {
  background: linear-gradient(45deg, #e74c3c, #c0392b);
  color: white;
}

.feedback-message {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.correct-answer {
  font-size: 1.3rem;
  margin-top: 10px;
  opacity: 0.9;
}

@keyframes feedbackSlide {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.name-input-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
}

.name-input-container label {
  font-size: 1.4rem;
  color: #667eea;
  font-weight: bold;
}

.name-input {
  font-size: 1.1rem;
  padding: 15px 20px;
  border: 3px solid #667eea;
  border-radius: 15px;
  text-align: center;
  width: 250px;
  font-family: inherit;
  background: white;
  will-change: contents;
  transform: translateZ(0);
}

.name-input:focus {
  outline: none;
  border-color: #764ba2;
  box-shadow: 0 0 10px rgba(102, 126, 234, 0.3);
}

.submit-button:disabled {
  background: #bdc3c7;
  cursor: not-allowed;
  transform: none;
}

.submit-button:disabled:hover {
  background: #bdc3c7;
  transform: none;
}

.current-results h2, .score-history h2 {
  color: #667eea;
  font-size: 1.5rem;
  margin: 20px 0 15px 0;
  text-align: center;
}

.score-history {
  margin-top: 25px;
  padding-top: 20px;
  border-top: 2px solid #f0f0f0;
}

.history-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.clear-history-button {
  background: none;
  border: none;
  font-size: 1.2rem;
  cursor: pointer;
  padding: 5px;
  border-radius: 5px;
  transition: background 0.2s;
}

.clear-history-button:hover {
  background: #f0f0f0;
}

.history-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-height: 200px;
  overflow-y: auto;
}

.history-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 15px;
  background: #f8f9fa;
  border-radius: 8px;
  border-left: 4px solid #4ecdc4;
}

.history-main {
  display: flex;
  gap: 15px;
  align-items: center;
}

.history-score {
  font-weight: bold;
  color: #667eea;
  font-size: 1.1rem;
}

.history-percentage {
  background: #4ecdc4;
  color: white;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 0.9rem;
  font-weight: bold;
}

.history-date {
  font-size: 0.8rem;
  color: #666;
  text-align: right;
}

.history-more {
  text-align: center;
  color: #999;
  font-style: italic;
  font-size: 0.9rem;
  padding: 10px;
}
